* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Inter', sans-serif;
    width: 100%;
}

nav {
    display: flex;
    justify-content: center;
    align-items: center;   
    height: 55px;
    padding: 20px 0;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

nav > img {
    max-width: 24px;
}

nav > h3{
    margin-inline-start: 10px;
    font-size: 15px;
}

.card {    
    padding-inline-start: 40px;
    padding-block-start: 45px;
    padding-block-end: 30px;
}

.card-section{
    display: flex;
}


hr {
   margin-block-start: 18px;
   margin-block-end:  18px;
   border: 1px solid #F5F5F5;
}
hr:last-of-type {
    display: none;
}
.card--img{
    width: 125px;
    height: 168px;
    object-fit: cover;
    border-radius: 5px;
}

.card--details {
    margin-inline-start: 20px;
}

.location {
    font-size: 10px;
    font-weight: 400;
    text-transform: uppercase;
    margin-inline-start: 5px; 
    margin-inline-end: 12px;
    color: #2B283A;
    letter-spacing: 0.17em; 
}

.locationUrl {
    font-size: 10px;
    font-weight: 400;
    color: #918E9B;
}

.card--details > h3{
    font-size: 25px;
    font-weight: 700;
    color: #2B283A;
    margin-block-start: 6px;
    margin-block-end: 15px; 
}

.card--details > p{
    font-size: 12px;
    color: #2B283A;
    line-height: 16px;
    text-align: justify;
    width: 70%;
}